import React from 'react'
import {
     FaFacebook, 
     FaInstagram, 
     FaTwitter, 
     FaYoutube 
    } from 'react-icons/fa';
import {
    FooterContainer,
    FooterWrap,
    SocialMedia,
    SocialMediaWrap,
    SocialLogo,
    SocialIcons,
    SocialIconsLink,
    // WebsiteRights
} from './FooterElements';


const footer = () => {
    return (
       <FooterContainer>
           <FooterWrap>
               <SocialMedia>
                   <SocialMediaWrap>
                       <SocialLogo to='/'> </SocialLogo>
                           {/*  <WebsiteRights>AL © {new Date().getFullYear()} All Rights reserved.</WebsiteRights> */}
                            <SocialIcons>
                                <SocialIconsLink href='https://www.facebook.com/princeqhaz/' target='_blank' aria-label='Facebook'>
                                    <FaFacebook />
                                </SocialIconsLink>
                                <SocialIconsLink href='https://www.facebook.com/princeqhaz/' target='_blank' aria-label='Instagram'>
                                    <FaInstagram />
                                </SocialIconsLink>
                                <SocialIconsLink href='https://www.facebook.com/princeqhaz/' target='_blank' aria-label='Youtube'>
                                    <FaYoutube />
                                </SocialIconsLink>
                                <SocialIconsLink href='https://www.facebook.com/princeqhaz/' target='_blank' aria-label='Twitter'>
                                    <FaTwitter/>
                                </SocialIconsLink>
                            </SocialIcons>
                   </SocialMediaWrap>
                </SocialMedia>
           </FooterWrap>
       </FooterContainer>
       )
}

export default footer
