import React from 'react'
import Icon1 from '../../images/profile.svg';
import Icon2 from '../../images/invitation.svg';
import Icon3 from '../../images/books.svg';
import Icon4 from '../../images/website.svg';
import Icon5 from '../../images/ui.svg';

import {
    ServicesContainer,
    ServicesH1,
    ServicesWrapper,
    ServicesCard,
    ServicesIcon,
    ServicesH2,
   /*  ServicesP */
} from './ServicesElements';

const LinkinPage = () => {
    return (
        window.open('https://www.pinterest.com/princeqhaz/pins/')
    );
};

const Services = () => {
    return (
        <ServicesContainer id='services'>
            <ServicesH1>Projects</ServicesH1>
            <ServicesWrapper>
                <ServicesCard onClick={LinkinPage} > 
                    <ServicesIcon src={Icon1} /> 
                    <ServicesH2>Company Profile</ServicesH2>
                    {/* <ServicesP>Help me think what will be the best designs for your company</ServicesP> */}
                </ServicesCard>
                <ServicesCard>
                    <ServicesIcon onClick={LinkinPage} src={Icon2} />
                    <ServicesH2>Invitation</ServicesH2>
                    {/* <ServicesP>We help reduce your fees and increase your overall revenue</ServicesP> */}
                </ServicesCard>
                <ServicesCard>
                    <ServicesIcon onClick={LinkinPage} src={Icon3} />
                    <ServicesH2>Books/Leaflets</ServicesH2>
      {/*               <ServicesP>We help reduce your fees and increase your overall revenue</ServicesP> */}
                </ServicesCard>
                <ServicesCard>
                    <ServicesIcon onClick={LinkinPage} src={Icon4} />
                    <ServicesH2>Websites</ServicesH2>
                    {/* <ServicesP>We help reduce your fees and increase your overall revenue</ServicesP> */}
                </ServicesCard>
                <ServicesCard>
                    <ServicesIcon onClick={LinkinPage} src={Icon5} />
                    <ServicesH2>UI Designs</ServicesH2>
                    {/* <ServicesP>We help reduce your fees and increase your overall revenue</ServicesP> */}
                </ServicesCard>
            </ServicesWrapper>
        </ServicesContainer>
    );
};

export default Services
