import React from 'react';
import {Button} from '../ButtonElements';

import {
     InfoContainerBg,
     InfoContainer,
     InfoWrapper,
     InfoRow,
     Column1,
     Column2,
     TextWrapper,
     TopLine,
     Heading,
     Subtitle,
     Subtitle2,
    //  BtnWrap,
     ImgWrap,
     Img
} from './InfoElements';

const InfoSection = ({
    lightBg, 
    id, 
    imgStart, 
    topLine, 
    lightText, 
    headline, 
    darkText,  
    description,
    description2,
    // buttonLabel, 
    img,
    alt,
    primary,
    dark,
    dark2
    }) => {
    return (
        <>
            <InfoContainerBg>  
               <InfoContainer lightBg={lightBg} id={id}>
               <InfoWrapper>
                   <InfoRow imgStart={imgStart}>
                       <Column1>
                            <TextWrapper>
                                <TopLine>{topLine}</TopLine>
                                <Heading lightText={lightText}>{headline}</Heading>
                                <Subtitle darkText={darkText}>{description}</Subtitle>
                                <Subtitle2 darkText={darkText}>{description2}</Subtitle2>
                              {/*   <BtnWrap>   
                                    <Button to='home' 
                                        smooth={true}
                                        duration={500}
                                        spy={true}
                                        exact="true"
                                        offset={-80}
                                        primary={primary ? 1 : 0}
                                        dark={dark ? 1 : 0}
                                        dark2={dark2 ? 1 : 0}
                                    >{buttonLabel}</Button>
                                </BtnWrap> */}
                            </TextWrapper>
                       </Column1>
                       <Column2>
                          <ImgWrap>
                            <Img src={img} alt={alt} />
                          </ImgWrap>
                       </Column2>
                   </InfoRow>
               </InfoWrapper>
            </InfoContainer>
          </InfoContainerBg> 
        </>
    );
};
 
export default InfoSection;