import React, {useState, useEffect} from 'react';
import {IconContext} from 'react-icons/lib';
import {FaBars} from 'react-icons/fa';
// import {animate as scroll} from 'react-scroll';
import {
    Nav,
    NavbarContainer, 
    // NavLogo, 
    MobileIcon, 
    NavMenu, 
    NavItem, 
    NavLinks,
    // NavBtn,
    // NavBtnLink
    } from './NavbarElements';

const Navbar = ({ toggle }) => {
    const [scrollNav, setScrollNav] = useState(false)

    const changeNav = () => {
        if(window.scrollY >= 200){
            setScrollNav(true)
        } else {
            setScrollNav(false)
        }
    }

    useEffect(() => {
        window.addEventListener('scroll', changeNav)
    }, [])

    // const toggleHome = () => {
    //     scroll.scrollToTop();
    // };

    return (
        <>
        <IconContext.Provider value={{ }}>
            <Nav scrollNav={scrollNav}>
                <NavbarContainer>
                    {/* <NavLogo to='/' onClick={toggleHome}>MyPortfolio</NavLogo> */}
                    <MobileIcon onClick ={toggle}>
                        <FaBars />
                    </MobileIcon>
                    <NavMenu>
                       <NavItem>
                           <NavLinks 
                                to='about'
                                smooth={true}
                                duration={500}
                                spy={true} 
                                exact='true' 
                                offset={-80} 
                           >
                               About</NavLinks>
                       </NavItem> 
                       <NavItem>
                           <NavLinks 
                           to='services'
                           smooth={true}
                           duration={500}
                           spy={true} 
                           exact='true' 
                           offset={-80}
                           >
                               Projects</NavLinks>
                       </NavItem>
                       <NavItem>
                           <NavLinks 
                           to='skills'
                           smooth={true}
                           duration={500}
                           spy={true} 
                           exact='true' 
                           offset={-80} 
                           >
                               Skills</NavLinks>
                       </NavItem> 
                       <NavItem>
                           <NavLinks 
                           to='experience'
                           smooth={true}
                           duration={500}
                           spy={true} 
                           exact='true' 
                           offset={-80} 
                           >
                               Experiences</NavLinks>
                       </NavItem> 
                    {/*    <NavItem>
                           <NavLinks
                            to='signin'
                            smooth={true}
                            duration={500}
                            spy={true} 
                            exact='true' 
                            offset={-20}                          
                            >
                                Message Me</NavLinks>
                       </NavItem>  */}
                    </NavMenu>
                    {/* <NavBtn>
                        <NavBtnLink to='/signin'>Sign In</NavBtnLink>
                    </NavBtn> */}
                </NavbarContainer>
            </Nav>
            </IconContext.Provider>
        </>
    );
};

export default Navbar;
