import styled from 'styled-components';

 export const InfoContainerBg = styled.div`
    background:
    radial-gradient(#E0E0E0 3px, transparent 4px),
    radial-gradient(#E0E0E0 3px, transparent 4px),
    linear-gradient(#fff 4px, transparent 0),
    linear-gradient(45deg, transparent 74px, transparent 75px, #E0E0E0 75px, #E0E0E0 76px, transparent 77px, transparent 109px),
    linear-gradient(-45deg, transparent 75px, transparent 76px, #E0E0E0 76px, #E0E0E0 77px, transparent 78px, transparent 109px),
    #fff;
    background-size: 109px 109px, 109px 109px,100% 6px, 109px 109px, 109px 109px;
    background-position: 54px 55px, 0px 0px, 0px 0px, 0px 0px, 0px 0px;
    border-bottom: solid 1px #E0E0E0;

    @media screen and (max-width: 1000px){
        padding: 10px 10px 30px;
        height: 370px;
    }

    @media screen and (max-width: 768px){
        padding: 20px 10px auto;
        height: 360px;
    }
    
`;
 
export const InfoContainer = styled.div`
   /*  color: #fff; 
    background: ${({lightBg}) => (lightBg ? '#f9f9f9' : '#010606')};  */
    z-index: 100;
    border-bottom: solid 1px #E0E0E0;
    width: auto;

    @media screen and (max-width: 768px) {
        padding: 50px 0 700px ;
    }
`;

export const InfoWrapper = styled.div`
    display: grid;
    z-index: 1;
    height: 560px;
    width: 100%;
    max-width: 1500px;
    margin-right: auto;
    margin-left: auto;
    padding:0 24px 0 0;
    justify-content: center;

    
    @media screen and (max-width: 1000px){
        padding: 10px 10px ;
        height: 360px;
    }
    @media screen and (max-width: 768px){
        height: 360px;
    }
    
`;

export const InfoRow = styled.div`
    display: grid;
    grid-auto-columns: minmax(auto, 1fr);
    align-items: center;
    grid-template-areas: ${({imgStart}) => (imgStart ? `'col2 col1'` : `'col1 col2'`)};
   

    @media screen and (max-width: 768px){
        grid-template: ${({imgStart}) => (imgStart ? `'col1' 'col2'` : `'col1 col1' 'col2 col2'`)}
        padding: 0 100px;
    }

`;

export const Column1 = styled.div`
    margin-bottom: 15px;
    margin-left: -60px;
    padding: 0 15px 0 0;
    grid-area: col1;

    @media screen and (max-width: 1028px){
        padding: 0 20px ;
    }
    @media screen and (max-width: 768px){
        padding: 0 40px ;
    }
`;

export const Column2 = styled.div`
    margin-bottom: 15px;
    padding: 0 15px;
    grid-area: col2;

`;

export const TextWrapper = styled.div`
    max-width: 540px
    padding-top: 0;
    padding-bottom: 60px;

    
`;

export const TopLine = styled.p`
    color: #01bf71;
    font-size: 16px;
    line-height: 16px;
    font-weight: 700;
    letter-spacing: 1.4px;
    text-transform: uppercase;
    margin-bottom: 16px;
`;

export const Heading = styled.h1`
    margin-bottom: 15px;
    font-size: 48px;
    text-transform: uppercase;
    line-height: 1.1;
    font-weight: 600;
    color: ${({lightText}) => (lightText ? '#f7f8fa' : '#010606')};
  
    @media screen and (max-width: 768px) {
        font-size: 32px;
    }
`;

export const Subtitle = styled.p`
    max-width: 700px;
    margin-bottom: 15px;
    font-size: 30px;
    line-height: 1.1;
    color: ${({darkText}) => (darkText ? '#010606' : '#fff')};
   
    @media screen and (max-width: 768px) {
        font-size: 16px;
    }
    
`;

export const Subtitle2 = styled.p`
    max-width: 700px;
    margin-bottom: 35px;
    font-size: 20px;
    line-height: 24px;
    color: ${({darkText}) => (darkText ? '#010606' : '#fff')};

    @media screen and (max-width: 768px) {
        font-size: 12px;
    }
    
`;

export const BtnWrap = styled.div`
    display: flex;
    justify-content: flex-start;

        
`;

export const ImgWrap = styled.div`
    max-width: 555px;
    height: 100%;

    
    @media screen and (max-width: 480px) {
        height: 100%;
    }
`;

export const Img = styled.img`
    width: 100%;
    margin: 0 0 10px 0;
    padding-right: 0;
    

    @media screen and (max-width: 480px) {
        font-size: 32px;
    }

`;


